@import url(https://fonts.googleapis.com/css?family=Montserrat:500,600,700);
* {
  margin: 0;
  scroll-behavior: smooth;
}

.App {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  font-family: "Montserrat", sans-serif;
}

.nav {
  position: fixed;
  width: 100%;
  height: auto;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  background-color: #d8d5c4;
  box-shadow: 0px 3px 16px #76767680;
}

.navItem {
  margin: 0 20px 0 20px;
  padding: 12px 20px 12px 20px;
  font-size: 20px;
  color: #868686;
  cursor: pointer;
  border-bottom: 5px solid transparent;
  transition: 0.3s;
}

.navItem:hover {
  color: #5a6e5a;
  border-bottom: 3px solid #5a6e5a;
}

.navItem.active {
  color: #5a6e5a;
  border-bottom: 3px solid #5a6e5a;
}

.aboutContainer {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  padding: 15px;
}

.aboutMe {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  padding: 80px;
  padding-bottom: 35px;
}

.aboutMe img {
  width: 400px;
  height: 400px;
  transition: 0.5s;
}

.aboutDesc {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-content: flex-start;
          align-content: flex-start;
  transition: 0.5s;
  padding: 15px;
}

.greeting {
  font-size: 30px;
  font-weight: bold;
  color: #4a4a4a;
  margin-bottom: 8px;
  -webkit-animation: slide-in-left 2s;
          animation: slide-in-left 2s;
}

@-webkit-keyframes slide-in-left {
  0% {
    -webkit-transform: translateX(-100px);
            transform: translateX(-100px);
    opacity: 0;
  }
  100% {
    -webkit-transform: translateX(0);
            transform: translateX(0);
    opacity: 1;
  }
}

@keyframes slide-in-left {
  0% {
    -webkit-transform: translateX(-100px);
            transform: translateX(-100px);
    opacity: 0;
  }
  100% {
    -webkit-transform: translateX(0);
            transform: translateX(0);
    opacity: 1;
  }
}

.name {
  font-size: 60px;
  color: white;
  margin-bottom: 10px;
  -webkit-animation: slide-in-right 2s;
          animation: slide-in-right 2s;
}

@-webkit-keyframes slide-in-right {
  0% {
    -webkit-transform: translateX(100px);
            transform: translateX(100px);
    opacity: 0;
  }
  100% {
    -webkit-transform: translateX(0);
            transform: translateX(0);
    opacity: 1;
  }
}

@keyframes slide-in-right {
  0% {
    -webkit-transform: translateX(100px);
            transform: translateX(100px);
    opacity: 0;
  }
  100% {
    -webkit-transform: translateX(0);
            transform: translateX(0);
    opacity: 1;
  }
}

.content {
  width: 50vw;
  font-size: 17px;
  color: #4a4a4a;
  -webkit-animation: slide-in-bottom 3s;
          animation: slide-in-bottom 3s;
}

@-webkit-keyframes slide-in-bottom {
  0% {
    -webkit-transform: translateY(150px);
            transform: translateY(150px);
    opacity: 0;
  }

  50% {
    opacity: 0;
  }

  100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
    opacity: 1;
  }
}

@keyframes slide-in-bottom {
  0% {
    -webkit-transform: translateY(150px);
            transform: translateY(150px);
    opacity: 0;
  }

  50% {
    opacity: 0;
  }

  100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
    opacity: 1;
  }
}

.contactButton {
  display: -webkit-flex;
  display: flex;
  margin: 30px;
  margin-left: 0;
}

.contactMe {
  width: 110px;
  height: 20px;
  padding: 10px;
  text-decoration: none;
  color: white;
  font-size: 17px;
  background-color: #5a6e5a;
  text-align: center;
  border-radius: 3px;
  transition: 0.1s;
}

.contactMe:hover {
  -webkit-transform: scale(1.02);
          transform: scale(1.02);
  box-shadow: 6px 6px white;
  transition: 0.2s;
}

.arrowBorder {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  width: 40px;
  height: 40px;
  background-color: #c05e51;
  border-radius: 100%;
  cursor: pointer;
}

.downArrow {
  border: solid #ffffff;
  border-width: 0 2px 2px 0;
  padding: 4px;
  transform: rotate(45deg);
  -webkit-transform: rotate(45deg);
  transition: 0.3s;
}

.arrowBorder:hover .downArrow {
  border-width: 0 3px 3px 0;
  padding: 5px;
  transition: 0.3s;
}

