.skillContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.skill {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.skillList {
  margin: 50px;
  padding: 0px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #ffffff;
  border-radius: 10px;
  width: 300px;
  height: 400px;
  cursor: pointer;
  box-shadow: 5px 6px 4px #44444485;
}

.skillItem {
  width: 300px;
  height: 400px;
}

.skillFront {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.skillIcon img {
  width: 150px;
  height: auto;
  transition: 0.8s;
}

.skillTitle {
  margin: 30px;
  font-size: 40px;
  color: gray;
  transition: 0.8s;
}

.skillDesc {
  position: absolute;
  margin: 0;
  padding: 0;
  top: 25px;
  overflow: hidden;
  color: lightgrey;
  white-space: nowrap;
}

.overlay {
  position: absolute;
  background-color: rgba(27, 27, 27, 0.979);
  border-radius: 10px;
  width: 300px;
  height: 0;
  transition: 1s;
  overflow-y: hidden;
}

.skillList:hover .overlay {
  width: 300px;
  height: 216px;
  transition: 1s;
}

.skillList:hover img {
  justify-self: flex-start;
  width: 115px;
  height: auto;
  transition: 0.8s;
}

.skillList:hover .skillTitle {
  margin: 10px;
  font-size: 25px;
  transition: 0.8s;
}

.arrowBorder {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 40px;
  height: 40px;
  background-color: #c05e51;
  border-radius: 100%;
  cursor: pointer;
}

.downArrow {
  border: solid #ffffff;
  border-width: 0 2px 2px 0;
  padding: 4px;
  transform: rotate(45deg);
  -webkit-transform: rotate(45deg);
  transition: 0.3s;
}

.arrowBorder:hover .downArrow {
  border-width: 0 3px 3px 0;
  padding: 5px;
  transition: 0.3s;
}
