.eduContainer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.listContainer {
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin-bottom: 45px;
}

.eduList {
  display: flex;
  flex-direction: column;
  margin: 20px;
  background-color: white;
  width: 250px;
  height: 380px;
  border-radius: 8px;
  box-shadow: 3px 5px 6px #44444456;
  cursor: pointer;
}

.eduImage img {
  border-radius: 10px 10px 0 0;
  width: 250px;
  height: 180px;
  transition: 0.5s;
  justify-self: center;
}

.eduDesc {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  margin: 10px;
  width: 80%;
  padding: 10px;
}

.eduName {
  font-size: 17px;
  margin-bottom: 15px;
}

.eduLoc {
  font-size: 15px;
  color: gray;
  margin: 5px;
}

.eduLoc img {
  margin-right: 10px;
  width: 15px;
}

.eduYear {
  font-size: 15px;
  color: gray;
  margin: 5px;
}

.eduYear img {
  margin-right: 15px;
  width: 20px;
}

.eduList:hover .eduImage img {
  justify-self: center;
  border-radius: 5px;
  transform: scale(1.1);
  transition: 0.5s;
  box-shadow: 2px 2px 4px #44444456;
}

.arrowBorder {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 40px;
  height: 40px;
  background-color: #c05e51;
  border-radius: 100%;
  box-shadow: 2px 2px 1px #44444456;
  cursor: pointer;
  transition: 0.3s;
}

.downArrow {
  border: solid #ffffff;
  border-width: 0 2px 2px 0;
  padding: 4px;
  transform: rotate(45deg);
  -webkit-transform: rotate(45deg);
  transition: 0.3s;
}

.arrowBorder:hover {
  width: 42px;
  height: 42px;
  box-shadow: 3px 3px 3px #44444456;
  transition: 0.3s;
}

.arrowBorder:hover .downArrow {
  border-width: 0 3px 3px 0;
  padding: 5px;
  transition: 0.3s;
}
