.aboutContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 15px;
}

.aboutMe {
  display: flex;
  justify-content: center;
  padding: 80px;
  padding-bottom: 35px;
}

.aboutMe img {
  width: 400px;
  height: 400px;
  transition: 0.5s;
}

.aboutDesc {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-content: flex-start;
  transition: 0.5s;
  padding: 15px;
}

.greeting {
  font-size: 30px;
  font-weight: bold;
  color: #4a4a4a;
  margin-bottom: 8px;
  animation: slide-in-left 2s;
}

@keyframes slide-in-left {
  0% {
    transform: translateX(-100px);
    opacity: 0;
  }
  100% {
    transform: translateX(0);
    opacity: 1;
  }
}

.name {
  font-size: 60px;
  color: white;
  margin-bottom: 10px;
  animation: slide-in-right 2s;
}

@keyframes slide-in-right {
  0% {
    transform: translateX(100px);
    opacity: 0;
  }
  100% {
    transform: translateX(0);
    opacity: 1;
  }
}

.content {
  width: 50vw;
  font-size: 17px;
  color: #4a4a4a;
  animation: slide-in-bottom 3s;
}

@keyframes slide-in-bottom {
  0% {
    transform: translateY(150px);
    opacity: 0;
  }

  50% {
    opacity: 0;
  }

  100% {
    transform: translateY(0);
    opacity: 1;
  }
}

.contactButton {
  display: flex;
  margin: 30px;
  margin-left: 0;
}

.contactMe {
  width: 110px;
  height: 20px;
  padding: 10px;
  text-decoration: none;
  color: white;
  font-size: 17px;
  background-color: #5a6e5a;
  text-align: center;
  border-radius: 3px;
  transition: 0.1s;
}

.contactMe:hover {
  transform: scale(1.02);
  box-shadow: 6px 6px white;
  transition: 0.2s;
}

.arrowBorder {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 40px;
  height: 40px;
  background-color: #c05e51;
  border-radius: 100%;
  cursor: pointer;
}

.downArrow {
  border: solid #ffffff;
  border-width: 0 2px 2px 0;
  padding: 4px;
  transform: rotate(45deg);
  -webkit-transform: rotate(45deg);
  transition: 0.3s;
}

.arrowBorder:hover .downArrow {
  border-width: 0 3px 3px 0;
  padding: 5px;
  transition: 0.3s;
}
