.nav {
  position: fixed;
  width: 100%;
  height: auto;
  display: flex;
  justify-content: center;
  background-color: #d8d5c4;
  box-shadow: 0px 3px 16px #76767680;
}

.navItem {
  margin: 0 20px 0 20px;
  padding: 12px 20px 12px 20px;
  font-size: 20px;
  color: #868686;
  cursor: pointer;
  border-bottom: 5px solid transparent;
  transition: 0.3s;
}

.navItem:hover {
  color: #5a6e5a;
  border-bottom: 3px solid #5a6e5a;
}

.navItem.active {
  color: #5a6e5a;
  border-bottom: 3px solid #5a6e5a;
}
